                name: 'PPC and anodised coatings',
                summary: 'PPC (Polyester Powder Coating) is a hard-wearing and durable coating specifically used externally on steel that requires long-term protection from exposure to extreme weather conditions or corrosive and abrasive environments.',
                image: '/static/images/repairs/ppcanodised.jpeg',
                content: '<p>PPC (Polyester Powder Coating) is a hard-wearing and durable coating specifically used externally on steel that requires long-term protection from exposure to extreme weather conditions or corrosive and abrasive environments.  Applied as a dry powder rather than a liquid paint, PPC creates a hard, protective layer on the steel structure and cures within minutes to an hour, reducing on-site wait time. This long-lasting coating can also be supplied in any colour to suit your design requirements making the steel both protected and pleasing to the eye. </p><p>Anodised coatings create added layers of corrosion-resistant protection on aluminium structures and surfaces. Anodised coated aluminium is a frequently used in construction as an alternative to expensive stainless steel as it is a lightweight yet sturdy material that can be used internally or externally. As an accredited contractor for major industry-leading brands we can ensure hard-wearing, durable coatings that are resistant to corrosion and salt, resistant to scratches, wont chip or peel and are available in many colour-fast, fade-resistant colours to suit requirements. </p>'
